.pharmacy_login_container .login {
	background-color: white;
	display: flex;
	border-radius: 50px;
	/* height: 24%; */
	height: 70px;
	width: 100%;
	cursor: pointer;
	color: white;
	background-color: hsla(88, 79%, 46%, 0.85);
	font-size: 2.5rem;
	align-items: center;
	cursor: pointer;
	justify-content: center;
	border: 1px solid #ccc;
	border: none;
	font-family: 'Avenir-Black';
	background: transparent linear-gradient(180deg, #83AE40 0%, #A2F622 100%) 0% 0% no-repeat padding-box;
	box-shadow: 0px 10px 6px #00000030;
	border-radius: 34px;
}

.pharmacy_login_container .label+.MuiInput-formControl {
	margin-top: 22px !important;
	border-bottom: 1.5px solid #707070;
}

.pharmacy_login_container button:focus {
	outline: none !important;
}

@media only screen and (max-width:768px) {
	.pharmacy_login_container .pharmacy_image {
		display: none;
		position: relative;
	}
	.pharmacy_login_container .pharmacy_main_container {
		width: 100%;
		height: auto;
		/* margin-top: 177px; */
	}
	.pharmacy_login_container .pharmacy_grid_container {
		width: 100%;
		height: auto !important;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	/* .pharmacy_login_container .Welcometext
{

  font-size: 20px !important;
} */
	.pharmacy_login_container .pharmacy_text {
		top: 14%;
		position: absolute;
	}
}

@media only screen and (max-width:959px) {
	.pharmacy_login_container .pharmacy_image_grid {
		display: none;
	}
	.pharmacy_login_container .pharmacy_grid_container {
		width: 100%;
		/* height:100vh !important; */
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.pharmacy_login_container .pharmacy_main_container {
		width: 70%;
		height: auto;
	}
	.pharmacy_login_container .pharmacy_main_container {
		width: 45%;
	}
	.pharmacy_login_container .Welcometext {
		font-size: 4rem !important;
	}
}

@media only screen and (max-width:1024px) {
