.confirm_password {
	background-color: #8bc137;
	border-radius: 50%;
	font-size: 2rem 1important;
	/* font-size: 25rem !important; */
	padding: 3px;
	position: relative;
	color: #fff;
	font-size: 2.5rem !important;
}

.success {
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 1.5rem;
	padding: 20px;
	color: #595959;
	font-family: 'Avenir-Black' !important;
}

.pharmacy_login_container .MuiInputBase-input {
	font-size: 15px !important;
	height: 3rem !important;
	padding: 6px 0 4px !important;
}

.confirm_container{
margin-top: 20px;
}


.errmsgclr{
	color: red;
	font-size: 14px;
	font-family: 'Avenir-Black' !important;
}

.errmsgMB{
	margin-bottom: 21px;
}

.errmsg_newpass div::before{
    border-bottom: 2px solid red;
}

.errmsg_newpass .MuiInput-underline:hover:not(.Mui-disabled):before {
    border-bottom: 2px solid red;
}

.errmsg_newpass div:hover::after{
    border-bottom: 2px solid red;
}

.errmsg_newpass .MuiInput-underline:after{
    border-bottom: 2px solid red !important;
}

